import React from 'react'
import Main from './Main'
import Todays from './Todays'
import NextDays from './NextDays'
import axios from 'axios'
let todaysArr = [];
let nextDaysArr = [];
class App extends React.Component{

    state = {lat:null, lon: null, weKey:'727e1baf6c9a5487365e0f7ab5ee71ec',mainData:null, currentLow:null, currentHigh:null, todaysData:[], nextDaysData:[]}

    componentDidMount(){
        navigator.geolocation.getCurrentPosition((position) => {
            this.setState({lat:position.coords.latitude, lon:position.coords.longitude})
            axios.get(`http://api.openweathermap.org/data/2.5/weather?lat=${this.state.lat}&lon=${this.state.lon}&units=metric&appid=${this.state.weKey}`,{mode:'cors'})
            .then((resp) =>{
                this.setState({mainData:resp.data})
            })
            axios.get(`http://api.openweathermap.org/data/2.5/onecall?lat=${this.state.lat}&lon=${this.state.lon}&units=metric&appid=${this.state.weKey}`,{mode:'cors'})
            .then((resp) =>{
                todaysArr = [];
                nextDaysArr = []
                resp.data.hourly.forEach(hour => {
                    
                    if(new Date(hour.dt*1000).getDate() === new Date(resp.data.hourly[0].dt*1000).getDate()){
                        return todaysArr.push(hour)
                    }
                });
                resp.data.daily.forEach(day => {
                    if(new Date(day.dt * 1000).getDate() !== new Date(resp.data.daily[0].dt * 1000).getDate() && new Date(day.dt * 1000).getDate() !== new Date(resp.data.daily[0].dt * 1000).getDate() + 7){
                        nextDaysArr.push(day)
                    }
                   
                    
                })
                this.setState({currentHigh:Math.floor(resp.data.daily[0].temp.max),currentLow:Math.floor(resp.data.daily[0].temp.min), todaysData:todaysArr, nextDaysData:nextDaysArr})
            })
        });
    }
    
    
    render(){
        if(this.state.mainData == null){
            return(
                <div className='my-loader'>
                <div className="ui">
                    <div className="ui active dimmer">
                        <div className="ui indeterminate text loader">Please Allow Your Location</div>
                    </div>
                    <p></p>
                </div>
                </div>
            )
        }else{
            
            return(
                <div>
                    <Main mainData={this.state.mainData} currentLow={this.state.currentLow} currentHigh={this.state.currentHigh}/>
                    <Todays todaysArr={this.state.todaysData}/>
                    <NextDays nextDaysArr={this.state.nextDaysData}/>
                </div>
            )
        }
        
    }
}

export default App;