import React from 'react'
import Slider from 'react-slick'
import {chooseIcon} from './chooseIcon'

class Todays extends React.Component{

    
    render(){
        const settings = {
            dots:false,
            arrows:false,
            infinite: true,
            slidesToShow: this.props.todaysArr.length >= 6 ? 6 : this.props.todaysArr.length,
            slidesToScroll: 1,
            autoplay: true,
            autoplaySpeed: 3000,
            pauseOnHover:false,
            responsive: [
                {
                    breakpoint: 1399.98,
                    settings: {
                      slidesToShow: this.props.todaysArr.length >= 5 ? 5 : this.props.todaysArr.length,
                      slidesToScroll: 1
                    }
                  },
              {
                breakpoint: 1199.98,
                settings: {
                  slidesToShow: this.props.todaysArr.length >= 4 ? 4 : this.props.todaysArr.length,
                  slidesToScroll: 1
                }
              },
              {
                breakpoint: 991.98,
                settings: {
                  slidesToShow: this.props.todaysArr.length >= 3 ? 3 : this.props.todaysArr.length,
                  slidesToScroll: 1
                }
              },
              {
                breakpoint: 767.98,
                settings: {
                  slidesToShow: this.props.todaysArr.length >= 2 ? 2 : this.props.todaysArr.length,
                  slidesToScroll: 1
                }
              },
              {
                breakpoint: 459.98,
                settings: {
                  slidesToShow:this.props.todaysArr.length >= 1 ? 1 : this.props.todaysArr.length,
                  slidesToScroll: 1
                }
              }
            ]
    };
        return(
            <section id="todays-weather">
    
    <div className="container">
        <div className="row">
            <div className="col-12">
            <p id="section-head">Todays Weather</p>
            </div>
        </div>
      <div className="row">
        <div className="col-12 my-5">
            <Slider {...settings}>
            {this.props.todaysArr.map((item,index) => {
                return (
                    <div className="card-today-weather" key={index}>
                    <p>{`${new Date(item.dt * 1000).getHours()}:00`}</p>
                    <i className={`${chooseIcon(item.weather[0].description.toLocaleLowerCase())}`}></i>
                    <div className="temperature">
                        <p>{`${Math.floor(item.temp)}`}</p>
                        <i className="far fa-circle"></i>
                    </div>
                </div>
                )
            })}
            </Slider>
        </div>
      </div>
    </div>

</section>
        )
    }
}

export default Todays;