import React from 'react'
import {chooseIcon} from './chooseIcon'
let dayNames = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
class NextDays extends React.Component{
    
    getDays = () => {
       return this.props.nextDaysArr.map((day,index) => {
       return( 
       <div className="col-12 mt-5" key={index}>
        <div className="card-next-day">
            <div className="row">
                <div className="col-lg-2 col-md-3 col-sm-4 col-6">
                    <div className="card-info">
                        <p className="info-top">{`${dayNames[new Date(day.dt*1000).getDay()].substring(0, 3)}`}</p>
                        <p className="info-bottom">{`${new Date(day.dt*1000).getDate()}/${new Date(day.dt*1000).getMonth()}`}</p>
                    </div>

                </div>
                <div className="col-lg-2 col-md-3 col-sm-4 col-6">
                    <div className="card-info">
                    <i className={`${chooseIcon(day.weather[0].description.toLocaleLowerCase())}`}></i>
                    </div>
                </div>
                <div className="col-lg-2 col-md-3 col-sm-4 col-6">
                    <div className="card-info">
                        <div className="info-top">
                            <p>{`${Math.floor(day.temp.max)}`}</p>
                            <i className="far fa-circle"></i>
                        </div>
                        <p className="info-bottom">High</p>
                    </div>
                </div>
                <div className="col-lg-2 col-md-3 col-sm-4 col-6">
                    <div className="card-info">
                        <div className="info-top">
                            <p>{`${Math.floor(day.temp.min)}`}</p>
                            <i className="far fa-circle"></i>
                        </div>
                        <p className="info-bottom">Low</p>
                    </div>
                </div>
                
                <div className="col-lg-2 col-md-3 col-sm-4 col-6">
                    <div className="card-info">
                        <p className="info-top">{`${Math.floor(day.wind_speed)}m/s`}</p>
                        <p className="info-bottom">Wind</p>
                    </div>
                </div>
                <div className="col-lg-2 col-md-3 col-sm-4 col-6">
                    <div className="card-info">
                        <p className="info-top">{`${day.humidity}%`}</p>
                        <p className="info-bottom">Humidity</p>
                    </div>
                </div>
    
            </div>
        </div>
    </div>
       )
    })}
    render(){
        return(
            <section id="next-days">
   
    <div className="container" style={{paddingBottom:'60px'}}>
        <div className="row mt-5">
            <div className="col-12">
            <p id="next-head">Next 6 Days</p>
            </div>
        </div>
        <div className="row">
           {this.getDays()}
        </div>
    </div>

</section>
        )
    }
}

export default NextDays;