import React from 'react'
import {chooseIcon} from './chooseIcon'
let dayNames = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
let monthNames = ["January", "February", "March", "April", "May", "June",
  "July", "August", "September", "October", "November", "December"
];
class Main extends React.Component{
    render(){
        const {sys,name,wind,main,dt,weather} = this.props.mainData;
        return(
            <section id="main-section">
    <div className="container">
        <div className="row">
            <div className="col-12">
                <div className="main-city-date">
                    <h2 id="main-city">{`${name}, ${sys.country}`}</h2>
                    <p id="main-date">{`${dayNames[new Date(1000*dt).getDay()]}, ${new Date(1000*dt).getDate()} ${monthNames[new Date(1000*dt).getMonth()]}`}</p>
                </div>
            </div>
        </div>
        <div className="row align-items-center justify-content-center">
            <div className="col-lg-6 mt-5">
                <div className="left">
                    <div className="main-weather-info">
                        <div className="icon"><i className={`${chooseIcon(weather[0].description.toLocaleLowerCase())}`}></i></div>
                        <div className="temperature-info">
                            <div className="temperature">
                                <p>{`${Math.floor(main.temp)}`}</p>
                                <i className="far fa-circle"></i>
                            </div>
                            <p className="weather-description">
                                {`${weather[0].description}`}
                            </p>
                        </div>
                    </div>
                    
                </div>
                

            </div>
            <div className="col-lg-6 mt-5">
                <div className="right">
                    <div className="info">
                        <div className="info-top-wrap">
                            <p className="info-top">{`${this.props.currentHigh}`}</p>
                            <i className="far fa-circle"></i>
                        </div>
                        <p className="info-bottom">High</p>
                    </div>
                    <div className="info">
                        <p className="info-top">{`${Math.floor(wind.speed)}m/s`}</p>
                        <p className="info-bottom">Wind</p>
                    </div>
                    <div className="info">
                        <p className="info-top">{`${new Date(1000*sys.sunrise).getHours()}:${new Date(1000*sys.sunrise).getMinutes()}`}</p>
                        <p className="info-bottom">Sunrise</p>
                    </div>
                    <div className="info">
                        <div className="info-top-wrap">
                            <p className="info-top">{`${this.props.currentLow}`}</p>
                            <i className="far fa-circle"></i>
                        </div>
                        <p classNme="info-bottom">Low</p>
                    </div>
                    <div className="info">
                        <p className="info-top">{`${main.humidity}%`}</p>
                        <p class="info-bottom">Humidity</p>
                    </div>
                    <div className="info">
                        <p className="info-top">{`${new Date(1000*sys.sunset).getHours()}:${new Date(1000*sys.sunset).getMinutes()}`}</p>
                        <p className="info-bottom">Sunset</p>
                    </div>
                </div>
            </div>

        
        </div>
    </div>
</section>
        )
    }
}

export default Main;